/* Game container */
.game-container {
    position: relative;
    width: 100%;
    height: 600px;
    background-color: #0a0a0a;
    overflow: hidden;
  }
  
  /* Start and Game Over screens */
  .start-screen,
  .game-over-screen {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    flex-direction: column;
    color: #fff;
  }
  
  .start-screen button,
  .game-over-screen button {
    padding: 15px 30px;
    font-size: 1.5rem;
    cursor: pointer;
  }
  
  /* Game area */
  .game-area {
    position: relative;
    width: 100%;
    height: 100%;
  }
  
/* Raccoon */
.raccoon {
    position: absolute;
    left: 20px;
    width: 60px;
    height: 60px;
    transform: translateY(0%);
    image-rendering: pixelated; /* Add this line */
  }
  
  /* Bug */
  .bug {
    position: absolute;
    width: 50px;
    height: 50px;
    image-rendering: pixelated; /* Add this line */

  }
  
  
  /* Scoreboard */
  .scoreboard {
    position: absolute;
    top: 10px;
    left: 10px;
    color: #fff;
    font-size: 1.2rem;
    display: flex;
    gap: 20px;
  }
  
  /* Mobile Controls */
.mobile-controls {
  position: absolute;
  bottom: 20px;
  right: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 10;
}

.control-button {
  background: rgba(255, 255, 255, 0.5);
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin: 5px 0;
  font-size: 24px;
  color: #000;
  cursor: pointer;
}

.control-button:focus {
  outline: none;
}

.control-button:active {
  background: rgba(255, 255, 255, 0.7);
}
